.butt {
  display: inline-block;
  border-radius: 4px;
  background-color: lightseagreen;
  border: none;
  color: #000000;
  text-align: center;
  font-size: 25px;
  padding: 20px;
  width: 200px;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
}

.butt span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.butt span:after {
  content: "\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.butt:hover span {
  padding-right: 25px;
  color: #ffffff;
}

.butt:hover span:after {
  opacity: 1;
  right: 0;
}

.bottomright {
  position: absolute;
  bottom: 8px;
  right: 16px;
  font-size: 15px;
  font-family: "Lucida Console", Courier, monospace;
  font-style: italic;
}

